.pagination-container{display:flex;flex-direction:row}.pagination-container>li{list-style:none}.pagination-container .back-button-container{position:relative;margin-right:16px;cursor:pointer}.pagination-container .next-button-container{position:relative;cursor:pointer}.pagination-container .break-button-container{position:relative;margin-right:16px}.pagination-container .break-button-container .break-button-label{user-select:none;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:white;line-height:5px;font-size:30px}.pagination-container .page-button-container{position:relative;cursor:pointer;margin-right:16px}.pagination-container .page-button-container .page-button-label{user-select:none;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:white;line-height:5px;font-size:18px;font-weight:700}
